import React from 'react';
import { motion } from 'framer-motion';

const Footer = () => {
  const currentYear = new Date().getFullYear();

  return (
    <footer className="bg-primary-dark py-8 px-6 border-t border-primary-medium">
      <div className="max-w-7xl mx-auto">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          {/* Brand */}
          <div>
            <motion.h3
              initial={{ opacity: 0 }}
              whileInView={{ opacity: 1 }}
              viewport={{ once: true }}
              className="text-2xl font-bold mb-4"
            >
              Rahul<span className="text-primary-light">.</span>
            </motion.h3>
            <p className="text-text-secondary">
              Data Scientist | Music Producer | Designer
            </p>
          </div>

          {/* Quick Links */}
          <div>
            <h4 className="text-lg font-semibold mb-4">Quick Links</h4>
            <ul className="space-y-2">
              <li>
                <a href="#about" className="text-text-secondary hover:text-primary-light transition-colors">
                  About
                </a>
              </li>
              <li>
                <a href="#projects" className="text-text-secondary hover:text-primary-light transition-colors">
                  Projects
                </a>
              </li>
              <li>
                <a href="#skills" className="text-text-secondary hover:text-primary-light transition-colors">
                  Skills
                </a>
              </li>
              <li>
                <a href="#contact" className="text-text-secondary hover:text-primary-light transition-colors">
                  Contact
                </a>
              </li>
            </ul>
          </div>

          {/* Social Links */}
          <div>
            <h4 className="text-lg font-semibold mb-4">Connect</h4>
            <ul className="space-y-2">
              <li>
                <a
                  href="https://linkedin.com/in/rahul-balhotra"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-text-secondary hover:text-primary-light transition-colors"
                >
                  LinkedIn
                </a>
              </li>
              <li>
                <a
                  href="https://github.com/rahulbalhotra"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-text-secondary hover:text-primary-light transition-colors"
                >
                  GitHub
                </a>
              </li>
              <li>
                <a
                  href="https://linktr.ee/rahulbalhotra"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-text-secondary hover:text-primary-light transition-colors"
                >
                  Linktree
                </a>
              </li>
            </ul>
          </div>
        </div>

        <div className="mt-8 pt-8 border-t border-primary-medium text-center text-text-secondary">
          <p>&copy; {currentYear} Rahul Balhotra. All rights reserved.</p>
        </div>
      </div>
    </footer>
  );
};

export default Footer; 