import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';

const ProjectCard = ({ title, description, category, tech, link }) => (
  <motion.div
    layout
    initial={{ opacity: 0 }}
    animate={{ opacity: 1 }}
    exit={{ opacity: 0 }}
    className="bg-primary-medium p-6 rounded-lg hover:shadow-xl transition-shadow"
  >
    <div className="flex justify-between items-start mb-4">
      <h3 className="text-xl font-bold">{title}</h3>
      <span className="text-sm px-3 py-1 bg-primary-light text-primary-dark rounded-full">
        {category}
      </span>
    </div>
    <p className="text-text-secondary mb-4">{description}</p>
    <div className="flex flex-wrap gap-2 mb-4">
      {tech.map((item, index) => (
        <span
          key={index}
          className="text-sm px-2 py-1 bg-primary-dark rounded-full"
        >
          {item}
        </span>
      ))}
    </div>
    {link && (
      <a
        href={link}
        target="_blank"
        rel="noopener noreferrer"
        className="text-primary-light hover:text-primary-accent transition-colors"
      >
        View Project 
      </a>
    )}
  </motion.div>
);

const Projects = () => {
  const [filter, setFilter] = useState('all');

  const projects = [
    {
      title: "Customer Segmentation & Sales Forecasting",
      description: "Developed ML models for customer segmentation and sales forecasting, processing 5M+ records with 90%+ accuracy.",
      category: "Data Science",
      tech: ["Python", "Prophet", "PowerBI", "Scikit-learn"],
      link: null
    },
    {
      title: "AI Strategy for Med-Tech",
      description: "Led AI strategy development, identifying 100+ AI solutions and developing PoC for operational efficiency.",
      category: "Data Science",
      tech: ["AI Strategy", "PoC Development", "Process Optimization"],
      link: null
    },
    {
      title: "Digital Transformation Strategy",
      description: "Orchestrated digital transformation for renewable energy company, achieving 26% operational efficiency increase.",
      category: "Data Science",
      tech: ["Digital Strategy", "Process Optimization", "KPI Development"],
      link: null
    },
    {
      title: "Contract GPT Assistant",
      description: "Developed RAG-based assistant using LangChain & GPT-4, saving 40+ hrs/week in contract processing.",
      category: "Data Science",
      tech: ["LangChain", "GPT-4", "RAG", "Python"],
      link: null
    },
    {
      title: "Telecom Assistant Chatbot",
      description: "Built chatbot system using JAMBA LLM model, integrated with MS Teams using N8N automation.",
      category: "Data Science",
      tech: ["Python", "JAMBA LLM", "N8N", "MS Teams"],
      link: null
    },
    {
      title: "Music Production Portfolio",
      description: "Electronic music production under the alias Sinetist, featuring original tracks and remixes.",
      category: "Music",
      tech: ["Ableton Live", "Sound Design", "Mixing", "Mastering"],
      link: "https://linktr.ee/rahulbalhotra"
    },
    {
      title: "Music Event Posters",
      description: "Collection of visually striking poster designs for music events and album releases.",
      category: "Design",
      tech: ["Photoshop", "Illustrator", "Typography"],
      link: null
    }
  ];

  const filteredProjects = filter === 'all' 
    ? projects 
    : projects.filter(project => project.category === filter);

  return (
    <section id="projects" className="min-h-screen bg-primary-dark py-20 px-6">
      <div className="max-w-7xl mx-auto">
        <motion.h2
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          className="text-4xl font-bold text-center mb-12"
        >
          Projects
        </motion.h2>

        <div className="flex justify-center gap-4 mb-12">
          {['all', 'Data Science', 'Music', 'Design'].map((category) => (
            <button
              key={category}
              onClick={() => setFilter(category)}
              className={`px-4 py-2 rounded-full transition-colors ${
                filter === category
                  ? 'bg-primary-light text-primary-dark'
                  : 'bg-primary-medium hover:bg-primary-medium/80'
              }`}
            >
              {category.charAt(0).toUpperCase() + category.slice(1)}
            </button>
          ))}
        </div>

        <motion.div 
          layout
          className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6"
        >
          <AnimatePresence>
            {filteredProjects.map((project, index) => (
              <ProjectCard key={project.title} {...project} />
            ))}
          </AnimatePresence>
        </motion.div>
      </div>
    </section>
  );
};

export default Projects; 