import React from 'react';
import { motion } from 'framer-motion';

const SocialLink = ({ href, children }) => (
  <motion.a
    href={href}
    target="_blank"
    rel="noopener noreferrer"
    whileHover={{ scale: 1.05 }}
    whileTap={{ scale: 0.95 }}
    className="flex items-center gap-2 text-primary-light hover:text-primary-accent transition-colors"
  >
    {children}
  </motion.a>
);

const Contact = () => {
  return (
    <section id="contact" className="min-h-screen bg-primary-dark py-20 px-6">
      <div className="max-w-7xl mx-auto">
        <motion.h2
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          className="text-4xl font-bold text-center mb-12"
        >
          Get in Touch
        </motion.h2>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-12">
          {/* Contact Information */}
          <motion.div
            initial={{ opacity: 0, x: -20 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: true }}
            className="space-y-6"
          >
            <h3 className="text-2xl font-bold mb-6">Contact Information</h3>
            
            <div className="space-y-4">
              <SocialLink href="mailto:rahulbalhotra@gmail.com">
                 rahulbalhotra@gmail.com
              </SocialLink>
              
              <SocialLink href="tel:+919115664460">
                 +91-9115664460
              </SocialLink>
              
              <SocialLink href="https://linkedin.com/in/rahul-balhotra">
                 LinkedIn
              </SocialLink>
              
              <SocialLink href="https://github.com/rahulbalhotra">
                 GitHub
              </SocialLink>
              
              <SocialLink href="https://linktr.ee/rahulbalhotra">
                 Linktree
              </SocialLink>
            </div>

            <div className="mt-8">
              <p className="text-text-secondary">
                Feel free to reach out for collaborations, opportunities, or just to say hi!
              </p>
            </div>
          </motion.div>

          {/* Contact Form */}
          <motion.div
            initial={{ opacity: 0, x: 20 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: true }}
          >
            <form className="space-y-6">
              <div>
                <label htmlFor="name" className="block text-sm font-medium mb-2">
                  Name
                </label>
                <input
                  type="text"
                  id="name"
                  name="name"
                  className="w-full p-3 rounded-lg bg-primary-medium border border-primary-light/20 focus:border-primary-light outline-none transition-colors"
                  required
                />
              </div>

              <div>
                <label htmlFor="email" className="block text-sm font-medium mb-2">
                  Email
                </label>
                <input
                  type="email"
                  id="email"
                  name="email"
                  className="w-full p-3 rounded-lg bg-primary-medium border border-primary-light/20 focus:border-primary-light outline-none transition-colors"
                  required
                />
              </div>

              <div>
                <label htmlFor="message" className="block text-sm font-medium mb-2">
                  Message
                </label>
                <textarea
                  id="message"
                  name="message"
                  rows="4"
                  className="w-full p-3 rounded-lg bg-primary-medium border border-primary-light/20 focus:border-primary-light outline-none transition-colors"
                  required
                ></textarea>
              </div>

              <motion.button
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                className="w-full py-3 px-6 bg-primary-light text-primary-dark rounded-lg font-semibold hover:bg-primary-accent transition-colors"
                type="submit"
              >
                Send Message
              </motion.button>
            </form>
          </motion.div>
        </div>
      </div>
    </section>
  );
};

export default Contact; 